@import "vars";
@import "libs";
@import "fonts";
@import "global";
.header{
  position: relative;
  height: 100vh;

}

.header__slider{
  &-item {
    background-image: url("../../build/img/header-bg.jpg");
    height: 100vh;
    background-repeat: no-repeat;
    background-size: cover !important;
    background-position: center;
    position: relative;
  }
}

.slider-item__info{
  position: absolute;
  right: 80px;
  bottom: 115px;
  text-align: right;
}

.slider-item__info-sutitle{
  margin-bottom: 5px;
}

.slider-item__info-suptext{
  margin-bottom: 8px;
}

.slider-item__info-title {
  font-weight: 800;
  font-size: 80px;
  line-height: 86px;
  margin-bottom: 48px;
}

.slider-item__info-text {
  font-weight: 800;
  font-size: 40px;
  line-height: 48px;
  margin-bottom: 20px;
}

.slider-arrows{
  position: absolute;
  top: 40px;
  right: 80px;
  cursor: pointer;
  z-index: 4;
}

.slider-arrows__left{
  right: 125px;
}

.slider-dotshead{
  position: absolute !important;
  right: 50px;
  bottom: 36px;
  max-width: 920px;
  width: 100%;
  z-index: 5;
  margin: 0;
  .slick-current{
    .dots-box{
      border-top: 4px solid $turquoise;
    }
  }
}
//.slick-track{
//  transform: none !important;
//}
.dots-box{
  width: 200px;
  display: flex;
  justify-content: space-between;
  height: 28px;
  align-items: center;
  padding-top: 6px;
  &__number{
    font-weight: 800;
    font-size: 24px;
    line-height: 34px;
  }
  &__name{
    font-weight: 300;
    line-height: 19px;
    text-transform: capitalize;
  }
}

.header__aside {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 5;
  border-right: 1px solid rgba(#fff, .25);
  height: 100vh;
  text-align: center;
}

.header__title{
  font-weight: 800;
  font-size: 200px;
  line-height: 156px;
  opacity: 0.07;
  max-width: 467px;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 4;
}

.menu{
  padding-top: 40px;
    a{
      span{
        display: block;
        font-weight: 500;
        font-size: 12px;
        line-height: 14px;
        letter-spacing: 3px;
        padding-top: 6px;
        margin-bottom: 20px;
        text-transform: capitalize;
      }
  }
}

.header__date{
  font-weight: 800;
  font-size: 72px;
  line-height: 64px;
  span{
    padding-top: 15px;
    font-weight: 300;
    font-size: 16px;
    line-height: 19px;
    letter-spacing: 1.4px;
    margin-bottom: 30px;
    display: block;
  }
}

.header__search{
  padding-top: 15px;
  padding-bottom: 35px;
}

.header__location{
  display: inline-block;
  font-weight: 300;
  font-size: 12px;
  line-height: 14px;
  letter-spacing: 1.05px;
  position: relative;
  padding-left: 19px;
  text-transform: capitalize;
  &::before {
    content: "";
    position: absolute;
    width: 11px;
    height: 13px;
    background-image: url("../../build/img/map-marker.svg");
    left: 0;
    top: 0;
    z-index: 5;

  }
}
.header__map{
  position: absolute;
  top: 0;
  right: 0;
  z-index: 3;
}

.header__arrows{
  position: absolute;
  left: 210px;
  bottom: 40px;
  z-index: 3;
}

.line-animation {
  stroke-dashoffset: 600;
  stroke-dasharray: 600;
  animation: animdash 4s infinite 5s;
}

@keyframes animdash {
  to{
    stroke-dashoffset: 0;
  }
}

//                surf

.surf{
  background-image: url("../../build/img/surf-bg.png");
  background-position: right 200px;
  background-repeat: no-repeat;
  background-size: auto;
}

.surf__subtitle{
  text-align: right;
  padding-right: 80px;
  margin-top: -86px;
}

.surf-slider{
  padding-top: 110px;
  .slider-arrows{
    top: 50px;
    opacity: .25;
  }
  .slick-current{
    .surf-box__inner-btn{
      display: block;
    }
    .surf-box__inner{
      background: rgba(#1c2121, .3);
    }
  }
  .slick-slide:nth-child(4n+1){
    .surf-box{
      margin-top: 32px;
    }
  }
  .slick-slide:nth-child(4n+3){
    .surf-box{
      margin-top: 89px;
    }
  }
}

.surf-box {
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover !important;
  min-height: 750px;
  &__inner{
    min-height: 750px;
    background: rgba(#1c2121, .85);
    color: #ffffff;
    position: relative;
    transition: all .5s;
    &:hover{
      background: rgba(#1c2121, .3);
      .surf-box__inner-btn{
        display: block;
      }
    }
  }
}
.surf-box__inner-title{
  font-weight: 800;
  font-size: 40px;
  line-height: 36px;
  padding: 72px 0 0 40px;
  width: 140px;
  text-transform: uppercase;
}
.surf-box__inner-location{
  font-size: 18px;
  line-height: 22px;
  letter-spacing: 8px;
  transform: rotate(-90deg);
  position: absolute;
  left: 40px;
  bottom: 80px;
  white-space: nowrap;
  width: 0;
  height: 0;
  span{
    font-weight: 100;
  }
}
.surf-box__inner-btn{
  display: none;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  text-align: center;
}

.btn{
  font-weight: 800;
  font-size: 40px;
  line-height: 36px;
  span{
    position: relative;
    padding: 17px 57px 17px 17px;
    display: inline-block;
    background-color: $turquoise;
    margin-left: 19px;
    &::before{
      content: '';
      position: absolute;
      width: 20px;
      height: 1px;
      background-color: #ffffff;
      top: 50%;
      left: -10px;
    }
    &::after{
      content: '';
      position: absolute;
      width: 25px;
      height: 21px;
      background-image: url("../../build/img/arrows-right.svg");
      top: 50%;
      right: 17px;
      transform: translateY(-50%);
    }
  }
}

.slider-map{
  max-width: 1200px;
  min-height: 635px;
  background-image: url("../../build/img/world_map_dots.png");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  margin: 0 auto;
  .slick-list{
    min-height: 635px;
  }
  .slick-current{
    .slider-dots__content{
      display: block;
      width: 285px;
      padding: 32px 32px 0 32px;
    }
  }
}

.slider-dots__content-row{
  display: flex;
  justify-content: space-between;
  padding-bottom: 30px;
}

.slider-dots__content-item{
  text-align: center;
  img{
    margin-bottom: 7px;
    display: inline-block;
  }
}

.slider-dots__content-number{
  font-weight: 800;
  font-size: 16px;
  line-height: 23px;
}
.slider-dots__content-name{
  font-weight: 300;
  font-size: 10px;
  line-height: 12px;
}

.slider-dots__content-title{
  font-weight: 800;
  font-size: 18px;
  line-height: 26px;
  padding-bottom: 8px;
  margin-bottom: 8px;
  position: relative;
  &::before{
    content: '';
    position: absolute;
    background-color: $turquoise;
    width: 80px;
    height: 1px;
    bottom: 0;
    left: 0;
  }
}
.slider-dots__content-subtitle{
  font-size: 16px;
  line-height: 19px;
  text-transform: capitalize;
}

.slider-dots__content-link{
  padding-top: 6px;
}

.slider-dots__circle{
  width: 21px;
  height: 21px;
  background-color: $turquoise;
  border-radius: 50%;
}

.slider-dots__content{
  display: none;
  background: rgba(#000, .7);
  border-radius: 6px;
  position: absolute;
  bottom: 35px;
  transform: translateX(-50%);
  &::before {
    border-bottom: 20px solid #4af6cd;
    border-left: 12px solid transparent;
    border-right: 12px solid transparent;
    border-style: solid;
    border-width: 0 10px 22px;
    border-color: transparent transparent $turquoise transparent;
    content: '';
    position: absolute;
    width: 0;
    height: 0;
    bottom: -23px;
    left: 50%;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
  }
}

.slider-dots{
  position: relative;
}

.slider-dots--1{
  left: 0;
  top: 335px;
  .slider-dots__content{
    transform: translateX(0);
    &::before{
      left: 0;
    }
  }
}
.slider-dots--2{
  left: 0;
  top: 270px;
  .slider-dots__circle{
    position: relative;
    &::before{
      content: '';
      position: absolute;
      background-image: url("../../build/img/mimi-marker.svg");
      width: 14px;
      height: 17px;
      background-size: cover;
      right: -20px;
      top: -15px;
    }
  }
}
.slider-dots--3{
  left: 25px;
  top: 330px;
}
.slider-dots--4{
  left: -25px;
  top: 470px;
}
.slider-dots--5{
  left: -65px;
  top: 230px;
}
.slider-dots--6{
  left: -120px;
  top: 550px;
}
.slider-dots--7{
  left: -90px;
  top: 375px;
}
.slider-dots--8{
  left: 35px;
  top: 470px;
  .slider-dots__content{
    transform: translateX(-262px);
    &::before{
      left: auto;
      right: 0;
    }
  }
}

// travel
.travel{
  background-image: url("../../build/img/travel-bg2.png");
  background-position: bottom right;
  background-repeat: no-repeat;
}
.travel-slider{
  &__head{
    background-repeat: no-repeat;
    background-position: top left;
    min-height: 780px;
  }
  &__descr{

  }
}





@import "media";